* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: 'Nunito', sans-serif;
  /* background-color: black!important;
  color: rgb(212, 194, 194); */
}

.countryy {
  /* box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22); */
}

.page-footer {
  background-color: rgb(39, 39, 39);
}

.paginationbtn {
  margin-top: 5%;
  margin-bottom: 15px;
  width: 80%;
  height: 40px;
  list-style: none;
  display: flex;
  justify-content: center;
  margin-left: 9%;
}

.paginationbtn a {
  padding: 10px;
  margin: 8px;
  border-radius: 5px;
  border: 1px solid #a5d0fa;
  color: #a5d0fa;
  cursor: pointer;
}

.paginationbtn a:hover {
  color: white;
  background-color: #a5d0fa;
  text-decoration: none;
}

.paginationactive a {
  color: white;
  background-color: #a5d0fa;
  text-decoration: none;
}

.theme:hover {
  cursor: pointer;
  color: rgb(44, 44, 44);
}

.cardd {
  background-color: rgb(56 54 54 / 59%) !important;
}

#load {
  animation: blinkk 1s linear infinite;
}

@keyframes blinkk {
  0% {
    opacity: 0;
  }

  50% {
    opacity: .5;
  }

  100% {
    opacity: 1;
  }
}

#pageload {
  position: relative;
  top: 27rem;
}

@media (min-width: 481px) and (max-width: 1024px) {
  #pageload {
    position: relative;
    top: 25rem;
  }

  .fontt {
    font-size: 15px;
  }

  .fonttt {
    font-size: 15px;
  }
}

@media (max-width: 480px) {
  .fontt {
    font-size: 15px;
  }

  .fonttt {
    font-size: 15px;
  }

  .paginationbtn {
    font-size: 10px;
    font-weight: bold;
  }

  #pageload {
    position: relative;
    top: 21rem;
  }
}